import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { UtilsProvider } from '../../providers/utils/utils';

/**
 * Generated class for the ListBankPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-list-bank',
  templateUrl: 'list-bank.html',
})
export class ListBankPage {

  public listBank: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, public utils: UtilsProvider) {

    //ObtenciÃ³n de los bancos
    this.storage.get('listBank').then((valor) => {
      this.listBank = valor;
    });
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ListBankPage');
  }

  bancoSeleccionado(bank){
     this.navCtrl.push("AddAccountsPage", { bancoSelected: bank } );
  }

  
logout(){
  this.utils.presentConfirm("LoginPage");
}

}



